@import '~node_modules/modern-normalize/modern-normalize.css';
:root {
  --font-family: "DM Sans", sans-serif;
  --black-color: #000;
  --red-color: #ae1332;
  --lime-color: #a4cb66;
  --yellow-color: #f5ce13;
  --white-color: #fff;
}

/* Global Styles */
* {
  margin: 0 auto;
  padding: 0;
  font-family: var(--font-family);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.main {
  background-color: var(--red-color);
}

.header {
  display: flex;
  justify-content: space-around;
  background-color: #fff;
  align-items: center;
  /* height: 50px; */
  margin: 16px 32px;
}

.header-nav-button {
  display: flex;
  font-weight: 400;
  font-size: 14px;
  line-height: 129%;
  letter-spacing: -0.04em;
  text-align: center;
  color: #ae1332;
}

.header-logo {
  display: flex;
  flex-direction: row;
}

.header-title {
  font-weight: 700;
  font-size: 14px;
  line-height: 129%;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  text-align: center;
  color: #ae1332;
}

svg {
  fill: #ae1332;
}

.hero-fruits-par {
  font-weight: 400;
  font-size: 14px;
  line-height: 129%;
  letter-spacing: -0.04em;
  color: var(--white-color);
}

.hero-card {
  font-weight: 400;
  font-size: 12px;
  line-height: 117%;
  letter-spacing: -0.04em;
  text-align: center;
  color: #fff;
  padding: 28px 0 61px 0;
}

.hero-left-img {
  width: 200px;
  height: 172px;
}

.hero-left-card-parg {
  font-weight: 500;
  font-size: 12px;
  line-height: 117%;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}

.hero-title {
  font-weight: 500;
  font-size: 64px;
  line-height: 100%;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}

.hero-pictures {
  border-radius: 500px;
  width: 452px;
  height: 702px;
}

.hero-shopnow-button {
  width: 114px;
  height: 114px;
  cursor: pointer;
  border: none;
  font-weight: 500;
  font-size: 28px;
  line-height: 100%;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  text-align: center;
  color: #ae1332;
  border-radius: 100%;
  fill: #fff;
  text-shadow: 0 4px 70px 0 rgba(0, 0, 0, 0.1);
  transform: rotate(-14deg);
}

.hero-shopnow-button:hover {
  color: var(--lime-color);
}

.hero-card-left-list {
  border: 1px solid #fff;
  border-radius: 1000px;
  padding: 12px 32px;
  width: 125px;
  height: 38px;
  font-weight: 500;
  font-size: 12px;
  line-height: 117%;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  text-shadow: 0 4px 70px 0 rgba(0, 0, 0, 0.1);
}

.hero-section {
  display: flex;
}

.hero-left-cards {
  border-radius: 500px;
  width: 249px;
  height: 387px;
  background: var(--lime-color);
  align-items: center;
  display: flex;
  flex-direction: column;
}

.hero-right-card {
  border-radius: 500px;
  width: 249px;
  height: 387px;
  background: var(--yellow-color);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero-right-img {
  width: 162px;
  height: 185px;
}

.test-buton {
  border: none;
  color: transparent;
  border-radius: 100px;
  padding: 15px 35px;
  width: 102px;
  height: 48px;
  background: #9dc162;
  position: relative;
  left: 190px;
}

.test {
  position: absolute;
  border: 1px solid #fff;
  border-radius: 100px;
  width: 292px;
  height: 48px;
  opacity: 0.3;
  color: none;
}

.test-dadad {
  margin: 30px;
}

.test-gif {
  border-radius: 20px;
  max-width: 100%;
  height: 476px;
}

.test-imgdiv {
  display: block;
  width: 860px;
  height: 100%;
}

.order-sec-title {
  font-weight: 500;
  font-size: 40px;
  line-height: 110%;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  text-shadow: 0 4px 70px 0 rgba(0, 0, 0, 0.1);
}

.order-sec-par {
  font-weight: 400;
  font-size: 14px;
  line-height: 129%;
  letter-spacing: -0.04em;
  text-align: center;
  color: #fff;
  text-shadow: 0 4px 70px 0 rgba(0, 0, 0, 0.1);
}

.order-list {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  width: 389px;
  height: 128px;
  background: #fff;
}

.list-title {
  border-radius: 200px;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 136px;
  height: 48px;
  background: #ae1332;
  font-weight: 400;
  font-size: 18px;
  line-height: 133%;
  letter-spacing: -0.04em;
  color: #fff;
}

.order-card-par {
  font-weight: 400;
  font-size: 14px;
  line-height: 129%;
  letter-spacing: -0.04em;
  color: #000;
  width: 166px;
  height: 48px;
}